
@charset "UTF-8";
#resumePreview {
  background: white;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
  padding: 2em;
  color: #333;
  line-height: 1.2;
  overflow: auto;
}
#resumePreview::after {
  content: "";
  display: block;
  clear: both;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
}
.slide_left_content {
  width: 38.196%;
  height: 1000px;
  float: left;
  background: #223;
}
.slide_right_content {
  width: 61.804%;
  height: 1000px;
  float: left;
  background: rgba(233, 233, 233, 0.7);
}
.layout_inside {
  width: 85%;
  margin: 0 auto;
}
.slide_left_content .layout_inside .self_content > div {
  text-align: center;
  width: 100%;
  color: white;
  border-bottom: 2px solid white;
}
.slide_left_content .layout_inside .self_content .auther {
  margin-top: 20px;
  height: 50px;
  font-size: 24px;
  line-height: 50px;
}
.slide_left_content .layout_inside .job_list {
  margin-top: 20px;
  line-height: 1.3em;
  font-size: 18px;
}
.slide_left_content .layout_inside .job_list p:nth-child(2) {
  font-size: 14px;
  margin-bottom: 16px;
}
.slide_left_content .layout_inside .about_me > ul {
  margin-top: 20px;
  margin-bottom: 20px;
}
.slide_left_content .layout_inside .about_me > ul > li {
  list-style: none;
  text-align: left;
  width: 100%;
  height: 30px;
  line-height: 30px;
  font-size: 12px;
}
.slide_left_content .layout_inside .about_me ul li span {
  text-align: right;
  display: inline-block;
  width: 70px;
}
.slide_left_content .layout_inside .technical h2 {
  width: 40%;
  height: 49px;
  line-height: 49px;
  font-size: 14px;
  float: left;
}
.slide_left_content .layout_inside .technical > div {
  height: 49px;
}
.slide_left_content .layout_inside .technical:after {
  content: '';
  display: block;
  clear: both;
}
.slide_left_content .layout_inside .technical .progress {
  float: left;
  width: 60%;
  height: 10px;
  background: #262626;
  padding: 0px;
  margin-bottom: 15px;
  overflow: visible;
  border-radius: 5px;
  border-bottom: 1px solid #7992a8;
  margin-top: 20px;
}
.slide_left_content .layout_inside .technical .progress .progress_bar {
  border-radius: 5px;
  height: 10px;
  position: relative;
  -webkit-animation: animate-positive 2s;
          animation: animate-positive 2s;
  background-image: linear-gradient(45deg, rgba(255, 255, 255, 0.15) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.15) 50%, rgba(255, 255, 255, 0.15) 75%, transparent 75%, transparent);
  background-size: 20px 20px;
  transition: width .6s ease;
}
.slide_left_content .layout_inside .technical .progress .progress_value {
  display: block;
  padding: 3px 7px;
  font-size: 10px;
  color: #fff;
  border-radius: 4px;
  background: #191919;
  border: 1px solid #000;
  position: absolute;
  top: -30px;
  right: -10px;
}
.slide_left_content .layout_inside .technical .progress .progress_value:after {
  content: " ";
  border-top: 10px solid #191919;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  position: absolute;
  bottom: -6px;
  left: 26%;
}
.slide_left_content .layout_inside .technical .progress_bar.active {
  background-color: #5bc0de;
  -webkit-animation: reverse progress_bar 0.40s linear infinite, animate-positive 2s;
          animation: reverse progress_bar 0.40s linear infinite, animate-positive 2s;
}
@-webkit-keyframes animate-positive {
0% {
    width: 0;
}
}
@keyframes animate-positive {
0% {
    width: 0;
}
}
@-webkit-keyframes progress_bar {
0% {
    background-position: 20px 0;
}
100% {
    background-position: 0 0;
}
}
@keyframes progress_bar {
0% {
    background-position: 20px 0;
}
100% {
    background-position: 0 0;
}
}
.slide_right_content .title {
  margin: 20px 0 10px 0;
  border-radius: 5px;
  background: #222;
  color: white;
  padding: 5px 20px;
}
.slide_right_content .my_technical ul li h3 {
  line-height: 1.5em;
  text-align: right;
  width: 100%;
  border-bottom: 2px dashed black;
}
.slide_right_content .my_technical ul li p {
  margin-top: 20px;
}
.slide_right_content .my_technical ul li {
  line-height: 1.5em;
}
.slide_right_content .my_projects ul li {
  width: 100%;
  height: 120px;
  margin-bottom: 10px;
}
.slide_right_content .my_projects ul li .project_message {
  padding-left: 145px;
  line-height: 1.3em;
}
.slide_right_content .my_projects ul li a {
  display: inline-block;
  border-bottom: 2px solid #222;
  color: #222;
}
.slide_right_content .my_projects ul li img {
  width: 130px;
  float: left;
}
.use_style {
  padding: 0px 6px;
  margin-left: 4px;
  display: inline-block;
  border-radius: 5px;
  color: #fff;
}
#熟悉 {
  width: 70%;
}
#精通 {
  width: 90%;
}
#一般 {
  width: 60%;
}
#了解 {
  width: 40%;
}
#resumePreview .preview {
  height: 50px;
  width: 50%;
  margin: 0 auto;
}
#resumePreview .preview .button {
  width: 160px;
  height: 50px;
  border-radius: 5px;
  background: #1abc9c;
}



